<template>
  <div>
    <v-dialog
      v-model="showDialog"
      :persistent="editMode"
      max-width="800"
      :fullscreen="isXs"
    >
      <v-card>
        <v-layout
          row
          wrap
        >
          <v-flex
            sm3
            xs6
          >
            <v-radio-group
              label="ランク"
              v-model="rank"
              :row="!isXs"
              v-if="editMode"
            >
              <v-radio
                v-for="rank in ranks"
                :label="rank"
                :value="rank"
                :key="rank"
                :disabled="!editMode"
                class="body-1"
                color="light-green lighten-1"
              ></v-radio>
            </v-radio-group>
          </v-flex>
          <v-flex
            sm9
            xs6
          >
            <v-radio-group
              label="種類"
              v-model="type"
              :row="!isXs"
              v-if="editMode"
              class="body-1"
            >
              <v-radio
                v-for="type in types"
                :label="type"
                :value="type"
                :key="type"
                :disabled="!editMode"
                color="brown lighten-2"
              ></v-radio>
            </v-radio-group>
          </v-flex>
          <v-flex
            sm4
            xs6
          >
            <v-radio-group
              label="装備"
              v-model="selectedEquipmentName"
              :mandatory="false"
            >
              <v-radio
                v-for="equipment in equipments"
                :label="formatEquipmentName(equipment)"
                :value="equipment.name"
                :key="equipment.name"
                :disabled="!editMode"
                class="caption"
              ></v-radio>
            </v-radio-group>
          </v-flex>
          <v-flex
            sm4
            xs3
          >
            <v-list :dense="isXs">
              <v-list-tile>
                詳細
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content :class="contentClass">ランク</v-list-tile-content>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content :class="contentClass">種別</v-list-tile-content>
              </v-list-tile>
              <v-divider />
              <v-list-tile>
                <v-list-tile-content :class="contentClass">射程</v-list-tile-content>
              </v-list-tile>
              <v-divider />
              <v-list-tile>
                <v-list-tile-content :class="contentClass">ダメージ</v-list-tile-content>
              </v-list-tile>
              <v-divider />
              <v-list-tile>
                <v-list-tile-content :class="contentClass">搭載可能部位</v-list-tile-content>
              </v-list-tile>
              <v-divider />
              <v-list-tile>
                <v-list-tile-content :class="contentClass">最低枚数</v-list-tile-content>
              </v-list-tile>
              <v-divider />
              <v-list-tile>
                <v-list-tile-content :class="contentClass">上限枚数</v-list-tile-content>
              </v-list-tile>
              <v-divider />
            </v-list>
          </v-flex>
          <v-flex
            sm4
            xs3
          >
            <v-list :dense="isXs">
              <v-list-tile>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content :class="contentClass">{{equipment.rank}}</v-list-tile-content>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content :class="contentClass">{{equipment.type}}</v-list-tile-content>
              </v-list-tile>
              <v-divider />
              <v-list-tile>
                <v-list-tile-content :class="contentClass">{{equipment.range}}</v-list-tile-content>
              </v-list-tile>
              <v-divider />
              <v-list-tile>
                <v-list-tile-content :class="contentClass">{{formatDamage(equipment)}}</v-list-tile-content>
              </v-list-tile>
              <v-divider />
              <v-list-tile>
                <v-list-tile-content :class="contentClass">{{equipment.mountPosition}}</v-list-tile-content>
              </v-list-tile>
              <v-divider />
              <v-list-tile>
                <v-list-tile-content :class="contentClass">{{equipment.formatMinLimit}}</v-list-tile-content>
              </v-list-tile>
              <v-divider />
              <v-list-tile>
                <v-list-tile-content :class="contentClass">{{equipment.maxLimit}}</v-list-tile-content>
              </v-list-tile>
              <v-divider />
            </v-list>
          </v-flex>
          <v-flex xs12>
            <v-card-text class="body-1">
              <span
                style="white-space: pre-wrap;display: inline-block;height:100px;"
                v-html="equipment.effect"
              ></span>
            </v-card-text>
          </v-flex>
          <v-spacer></v-spacer>
          <v-flex xs3 />
          <v-flex xs3>
            <v-select
              v-if="editMode"
              :items="itemcounts"
              v-model="itemcount"
              label="追加装備数"
              item-value="text"
            ></v-select>
          </v-flex>
          <v-flex xs3>
            <v-btn
              v-if="editMode"
              color="green darken-1"
              flat
              @click.native="select"
            >選択する</v-btn>
          </v-flex>
          <v-flex xs3>
            <v-btn
              color="green darken-1"
              flat
              @click.native="closeDialog"
            >閉じる</v-btn>
          </v-flex>
        </v-layout>
      </v-card>
      <ins
        class="adsbygoogle"
        style="display:block"
        data-ad-client="ca-pub-9097509632200457"
        data-ad-slot="2021913901"
        data-ad-format="auto"
        data-full-width-responsive="true"
      ></ins>
    </v-dialog>
  </div>
</template>

<style>
</style>

<script>
import Equipment from "@/model/embriomachine/equipment";

export default {
  name: "EquipmentSeletorDialog",
  props: {
    //ダイアログ表示フラグ。
    showDialog: {
      type: Boolean,
      default: true
    },
    //編集対象の装備データ。未選択の場合はnullを指定。
    targetEquipment: {
      type: Object,
      default: null
    },
    //ダイアログ表示フラグ。
    targetPosition: {
      type: String,
      default: null
    },
    //編集モードかどうかの指定。
    editMode: {
      type: Boolean,
      default: true
    }
  },

  data() {
    return {
      selectedEquipmentName: this.targetEquipment.name,
      types: ["射撃", "白兵", "機雷", "装甲", "補助", "その他"],
      type: "射撃",
      ranks: ["A", "B"],
      selectedRanks: ["B"],
      rank: "B",
      itemcounts: [1, 2, 3],
      itemcount: 1
    };
  },

  watch: {
    targetEquipment: function(val) {
      if (val != null) {
        this.selectedEquipmentName = val.name;
        this.type = val.type;
        this.rank = val.rank;
        if (this.type === "" || this.type === undefined) {
          this.type = "射撃";
          this.rank = "B";
        }
      } else {
        this.selectedEquipment = "";
        this.type = "射撃";
        this.rank = "B";
      }
    },
    equipment: function(val) {
      this.itemcount = val.minLimit;
    },
    showDialog: function(val) {
      if (!val) {
        this.closeDialog();
      }
    }
  },

  computed: {
    equipments() {
      return Equipment.getEquipments().filter(equipment => {
        if (this.type !== null) {
          if (equipment.type !== this.type) {
            return false;
          }
          if (equipment.rank !== this.rank) {
            return false;
          }
        }
        if (this.targetPosition === null) {
          return true;
        } else {
          return equipment.canEquip(this.targetPosition);
        }
      });
    },

    equipment() {
      let ret = {};
      this.equipments.forEach(item => {
        if (item.name === this.selectedEquipmentName) {
          ret = item;
        }
      });
      return ret;
    },
    contentClass() {
      if (this.$vuetify.breakpoint.name === "xs") {
        return "caption";
      } else {
        return "subheading";
      }
    },
    isXs() {
      return this.$vuetify.breakpoint.name === "xs";
    }
  },

  methods: {
    closeDialog() {
      // this.$emit("update:targetEquipment", {});
      this.$emit("update:showDialog", false);
      this.$emit("cancel");
      this.selectedEquipmentName = "";
      this.itemcount = 1;
    },
    select() {
      // this.$emit("update:targetEquipment", this.equipment);
      this.$emit("update:showDialog", false);
      this.$emit("select", this.equipment, this.itemcount);
      this.selectedEquipmentName = "";
      this.itemcount = 1;
    },
    //FIXME deprecated.
    formatEquipmentName(equipment) {
      return equipment.name;
    },
    formatDamage(equipment) {
      if (
        equipment === null ||
        equipment === undefined ||
        equipment.damage === undefined
      ) {
        return "";
      } else {
        return equipment.damage + "(" + equipment.damageType + ")";
      }
    }
  }
};
</script>



